<template>
<div style="height:100%;">
  <div class="index-main">
    <div class="index-main-body">
      <div class="index-header">
        <h1 class="logo hide-text"></h1>
        <h2 class="subtitle">&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;</h2>
      </div>
      <form class="ui form">
        <div class="ui basic segment">
          <div class="field"><input placeholder="" type="text" ref="account" /></div>
          <div class="field"><input placeholder="" type="password" ref="password" /></div>
          <div class="ui green right labeled icon fluid button" @click="login()"><i class="sign in icon"></i></div>
        </div>
      </form>
    </div>
  </div>
  <div class="footer">
    <span> 2017 </span>
    <span class="dot"></span>
    <a target="_blank" href="http://www.huizhong.co/"></a>
    <span class="dot"></span>
    <a target="_blank" href="http://software.huizhong.co/center/"></a>
    <span class="dot"></span>
    <a target="_blank" href="http://software.huizhong.co/manual/en.html"></a>
  </div>
</div>
</template>
<script>
import axios from 'axios';
export default {
  data() {
    return {}
  },
  methods: {
    login() {
      let account = this.$refs.account.value;
      let password = this.$refs.password.value;
      if (this.str.isEmpty(account) || this.str.isEmpty(password)) {
        this.toast.error("");
      } else {
        let password_md5 = $.md5(password);
        axios.post('/resource/dynamic/manager/token', {
          'adminAccount': account,
          'adminPassword': password_md5
        }).then(response => {
          let status = response.data.status;
          if (status == 'SUCCESS') {
            this.token.setToken(response.data.content);
            this.$router.push({
              path: '/software'
            });
          } else {
            this.toast.error(response.data.message.notice);
          }
        }).catch(function(error) {
          this.toast.error('');
        });
      }
    }
  },
  mounted: function() {
    document.onkeyup = function(e) { //e
      var code = e.charCode || e.keyCode; //(keyCodecharCode)
      if (code == 13) {
        //
        this.login();
      }
    }.bind(this);
  }
}
</script>
<style>
/*.index-main .ui.button {
  background: #21BA45 none;
  color: #FFFFFF;
}

.index-main .ui.button:hover {
  background-color: #178C32;
  background-image: none;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34, 36, 38, .15) inset;
  color: #FFFFFF;
}*/
</style>



